<template >
	<div class="xiaozu" @click="fn" >
		<ding txts="小组" >
			<img src="../../assets/img1/ic_search_gray1.png" class="shous" />
			<img src="../../assets/img1/ic_chat_white1.png" class="duihua" />
		</ding>
		<div class="zhanwei">
			
		</div>
		<Groupinner></Groupinner>
		<Groupfilms></Groupfilms>
		<Groupfilms></Groupfilms>
		<Groupfilms>	</Groupfilms>
		
	</div>
</template>


<script>
	import ding from"../../components/ding"
	import Groupinner from './groupinner'
	import Groupfilms from './groupfilms'
	
export default {
  components:{
  	ding,
  	Groupfilms:Groupfilms,
	Groupinner:Groupinner,
  },
		computed:{
			bu:function(){
				if(this.xiab==this.jilu){
					return false
				}
				return true
			}
		},
		methods:{
			fn:function(){
				//console.log( "xia:"+this.xiab  )
				//console.log( "点击:"+this.jilu  )
				this.$emit("dianjii",this.xiab)
			}
	}
}

</script>

<style >
	.xiaozu{background: #fff; padding-bottom: .95rem; }
	.xiaozu .a1{width: 100%;height: 100%; }
</style> 



