<template >
	<div class="box1"  >
			
		<div class="swiper-container" :class='swipeid'>
    		<div class="swiper-wrapper">
		        <slot name='bannerImg1'></slot>
				
		    </div>
    		<div :class='{"swiper-pagination":pagination}' :style="{'text-align':paginationDirection}"></div>
   	 	</div>
			
	</div>
</template>

<script>
	import "../../assets/js/swiper.js";
	
export default {
  
props:{
      swipeid:{
        type:String,
        default:''
      },
      loop:{		//是否循环
        type:Boolean,
        default:true
      },
      direction:{	//滑动方向
        type:String,
        default:'horizontal'//水平  	vertical 垂直
      },
      autoplay:{	//间隔时间
        type:Number,
        default:5000
      },
      effect:{
      	default:'slide'	//切换效果 slide 默认  "cube" 有阴影  		正方形切"coverflow"	翻页"flip" 	fade（淡入）
      },
      paginationType:{	//分页器类型
        type:String,
        default:'bullets'	//‘bullets’  圆点（默认）‘fraction’  分式 ‘progress’  进度条  ‘custom’ 自定义
      },
      pagination:{		//分页器
        type:Boolean,
        default:true
      },
      paginationDirection:{		//居中
        type:String,
        default:'center'
      },
      speed:{
      	default:1000
      },				//从滑动过程的时间，
    },
    mounted:function(){
      var _this=this;
      new Swiper('.'+_this.swipeid,{
        
        loop:_this.loop,
        
        pagination:".swiper-pagination",
        
        direction:_this.direction,
        
        autoplay:_this.autoplay,
        effect:_this.effect,	//切换效果slide默认  "cube" 有阴影  		正方形切"coverflow"	翻页"flip" 	fade（淡入）		
        speed:_this.speed,				//从滑动过程的时间，
  		//autoplayDisableOnInteraction:false,	//用户操作swiper之后，是否禁止autoplay。默认为true：停止。
  		grabCursor:true,	//设置为true时，鼠标覆盖Swiper时指针会变成手掌形状，拖动时指针会变成抓手形状。
  		freeMode : false,	//启动可以滑动多格
  		freeModeMomentumVelocityRatio:2,	//惯性速度
  		freeModeSticky:true,	//启用边缘贴合
  		freeModeMomentumBounce:true,	//启用反弹
  		freeModeMomentumBounceRatio:3,	//反弹力度
  		zoom:true,
      });
    }
  }

</script>

<style scoped="">
	@import "../../assets/css/swiper.css";
	
	.swiper-container img{height:2.39rem;width:100%;}
</style> 



