<template >
	<div class="shouye" >
	<sousuo> </sousuo>
	<div class="zhanwei">
		
	</div>
	<lunbo swipeid='swipeid01' :autoplay=1000>
		<div class="swiper-slide" slot="bannerImg1" >	
			<div class="swiper-zoom-container">
				<img src="../../assets/img1/banner/01.jpg"/>
			</div>	
		</div>
		<div class="swiper-slide" slot="bannerImg1" >
			<div class="swiper-zoom-container">
				<img src="../../assets/img1/banner/02.jpg"/>
			</div>
		</div>
		<div class="swiper-slide" slot="bannerImg1" >
			<div class="swiper-zoom-container">
				<img src="../../assets/img1/banner/03.jpg"/>
			</div>
		</div>
</lunbo>

	<hotspot></hotspot>
		<hotspotwrap>
			<h2 slot='h2'> {{ title[0] }}  </h2>
			<p slot='p'> {{ nei[0] }}  </p>
			<div slot='line'></div>
			<span slot='author'>作者：{{  zuozhe[0] }} </span>
			<i slot='line'></i>
			<img src='../../assets/img1/4_03.jpg' slot='img'/>
		</hotspotwrap>
		<hotspotwrap>
			<h2 slot='h2'>  {{ title[1] }} </h2>
			<p slot='p'>{{ nei[1] }}</p>
			<div slot='line'></div>
			<span slot='author'>作者：{{  zuozhe[1] }}</span>
			<i slot='line'></i>
			<p slot='comeFrom'>来自栏目：{{ lanm[1] }} </p>
			<img src='../../assets/img1/4_06.jpg' slot='img'/>
		</hotspotwrap>
		<hotspotwrap>
			<h2 slot='h2'>《加勒比海盗》14年老粉给你带来所有彩蛋和深埋梗</h2>
			<p slot='p'>此影评送给那些不离不弃的老粉和船长杰克.斯派罗</p>
			<div slot='line'></div>
			<span slot='author'>作者：女神的秋裤</span>
			<i slot='line'></i>
			<p slot='comeFrom'>来自栏目：一周热门影视</p>
			<img src='../../assets/img1/4_03.jpg' slot='img'/>
		</hotspotwrap>

	</div>
</template>


<script>
	import sousuo from"../../components/sousuo"
	import lunbo from"./lunbo"
	import hotspot from './hotspot'
	import hotspotwrap from './hotspotwrap'
    	
export default {
	data:function (){
	    return {
	      title: [
	      "《加勒比海盗》14年老粉给你带来所有彩蛋和深埋梗",
	      "一周豆瓣热门影视|二十年后，这帮苏格兰老炮儿还是那么嗨",
	      ],
	      nei: [
	      "此影评送给那些不离不弃的老粉和船长杰克.斯派罗",
	      "三个榜单的冠军与上奏一样，冠军们大概孤独求败....",
	      ],
	      zuozhe:["女神的秋裤","豆瓣"],

	      lanm:["0","一周热门影视"]
	    }
	 },
	components:{
		sousuo,
		lunbo,
		hotspot:hotspot,
	    hotspotwrap:hotspotwrap
	},
	methods:{
		fn:function(){
			//this.$emit("dianjii",this.xiab)
		}
	}
}

</script>

<style>
	.zhanwei{height: 1.25rem;width: 100%;}
	.shouye{background: #f7f7f7; padding-bottom: .95rem; }
</style> 



