<template>
	<div class="box1" @click="fn" >
		<span v-show="bu" ><slot name="bai"></slot></span>		
		<span v-show="!bu" ><slot name="hong"></slot></span>
		<p :class="{red:!bu}" > {{ txts }} </p>
	</div>
</template>

<script>
	
	export default {
		
	props:["txts","xiab","jilu"],
	methods:{
		fn(){
			//console.log(this.xiab)
			//console.log(this.jilu)
			this.$emit("dianji", this.xiab )
			this.$router.push("/"+this.xiab)     //跳转当前页面，并创建历史记录
		}
	},
	computed:{
			bu:function(){
				if(this.xiab==this.jilu){
					return false
				}
				return true
			}
		}
	}
</script>

<style >
	.box{z-index: 100;text-align:center;background: #fff ;width: 100%; height: .95rem; position: fixed; color:#000;font-size:.44rem;line-height:.44rem;bottom: 0; left: 0;border-top:0.01rem #ccc solid; }
			.box .box1{float: left;width: 20%; }
			.box img{width: .60rem; }  
			.box p{color: #CCCCCC;  font-size:.25rem;line-height:.25rem;}  
			.box .red{color: #50B95F; }  
</style>


