<template >
	<div class="ding0">
		<div class="zhuangt"><img src="../assets/img1/zhuangt1.jpg"/></div>
		<div class="ding" @click="fn" >
			<span >{{ txts }}</span>
			<slot  > </slot>
			<img src="../assets/img1/ic_settings.png" v-show="wod" class="wodtu"  />
		</div>
	</div>
</template>


<script>
	
export default {
	data(){
		return {
			
		}
	},
   props:["txts",'wod'],
		computed:{
			bu:function(){
				if(this.xiab==this.jilu){
					return false
				}
				return true
			}
		},
		methods:{
			fn:function(){
				//console.log( "xia:"+this.xiab  )
				//console.log( "点击:"+this.jilu  )
				this.$emit("dianjii",this.xiab)
			}
	}
}

</script>

<style >
	.ding0{position:fixed;top:0;left: 0; z-index: 100;width: 100%; }
	.zhuangt{width: 100%;height:0.35rem;background: #42C155;}
	.zhuangt img{width: 100%;height: 0.362rem;}
	.ding{text-align:center;background: #F8F9F8 ; height: .9rem;position: relative; border-bottom: .01rem solid #ccc;}
	.ding span{text-align:center;color:#423939;font-size:.35rem;line-height:1rem;font-weight:bold;}
	.ding img{position: absolute; width: .45rem;}
	.ding .shous{top: .2rem;right:1.2rem; }
	.ding .wodtu{top: .2rem;right:.35rem;width: .45rem; }
	.ding .duihua{width: .450rem; top: .18rem;right:.350rem ; }	
</style> 



