<template id="">
		<div class="guangbo" >
			<ding txts="推荐广播" >
				<img src="../../assets/img1/ic_status_search_user.png" class="shous" />
				<img src="../../assets/img1/ic_chat_white1.png" class="duihua" />
			</ding>
			<div class="zhanwei"></div>

			<div class="follow">
			<img src="../../assets/img1/ic_status_.png" class="inter" />
			<h2>发现有趣的人</h2>
			<span>关注他们,发现更大的世界</span>
		</div>
		<Dynamyc txt3 ='陈苍苍' :jilu=xiab @dianjii="fn" txt4 = '53288'  >
			<img src="../../assets/img1/giay_pic_10.jpg" slot = 'dynamycImg'/>
			<p slot = 'linek'>50</p>
			<p slot = 'gares'>29</p>
			<p slot = 'texted'>我们没有小孩的也很不容易的啊 ！在家被别人的小孩吵，三天两头躲出去不就是为了图个清静吗！出门在外接着被各种熊孩子打扰，坐个飞机小孩喊一路坐个高铁小孩也嚎一路，很不容易！help！哭哭，我化妆我健身我努力维持体面，为什么这么多小孩不肯放过我</p>
		</Dynamyc>
		<Dynamyc txt3 ='绿箜' :jilu=xiab @dianjii="fn" txt4 = '1142'>
			<img src="../../assets/img1/giay_pic_07.jpg" slot = 'dynamycImg'/>
			<p slot = 'linek'>23</p>
			<p slot = 'gares'>16</p>
			<p slot = 'texted'>菜市场的颜色真是漂亮极了。有非常多我喜欢吃的东西,非常多新鲜的蔬菜，水果，海鲜，等等。今天回去要买好多的菜，好好犒劳下自己，最近确实上班辛苦。晚上大餐，红烧肉，清蒸鲈鱼，油焖大虾，是不是隔着手机屏幕都觉得香啊，哈哈，晚上再来防毒.....</p>
		</Dynamyc>
		<Dynamyc txt3 ='怪物猎人' :jilu=xiab @dianjii="fn" txt4 = '53288'  >
			<img src="../../assets/img1/westernnewdish_1.png" slot = 'dynamycImg'/>
			<p slot = 'linek'>50</p>
			<p slot = 'gares'>29</p>
			<p slot = 'texted'>#深夜发猫#这是之前去某位设计师的工作室拍的。她的助手还追出来送我和小伙伴一人一只小熊，布料很赞</p>
		</Dynamyc>
		<Dynamyc txt3 ='撒旦的表妹' :jilu=xiab @dianjii="fn" txt4 = '53288'  >
			<img src="../../assets/img1/wodtx.jpg" slot = 'dynamycImg'/>
			<p slot = 'linek'>50</p>
			<p slot = 'gares'>29</p>
			<p slot = 'texted'>这一期的中国有嘻哈看得我气死了，这孤儿赛制在想啥，分组不是为了各组pk而是组内厮杀？一个个淘汰是养蛊吗？投票也是，一堆忘词好好的没错没忘没抢拍特别real的给淘汰了，这看脸的世界。一开始还每期都有还几首好听的歌，现在一期就三首还车祸现场。宣传嘻哈音乐结果噱头 都放在真人秀上，呵呵</p>
		</Dynamyc>
		<Pick></Pick>
		<div class="guangb_zhanwei">		</div>
			<!--<span v-show="bu">  <slot name="bai" > </slot>  </span>
			<span v-show="!bu"> <slot name="hong"> </slot>  </span>
			<p :class="{red:!bu }" > {{ xia }} </p>-->
		</div>
</template>


<script>
	import ding from"../../components/ding"
	import Dynamyc from './dynamyc'
	import Pick from './pick'
export default {
	data(){
		return {
			xiab:""
		}
	},
	components:{
  	ding,
  	Dynamyc:Dynamyc,
	Pick:Pick,
  },
  props:[ 'xx'],
		computed:{
			/*bu:function(){
				if(this.xiab==this.jilu){
					return false
				}
				return true
			}*/
		},
		methods:{
			fn:function(ev){
				this.xiab=ev
			}
	}
}

</script>

<style scoped>
	.guangb_zhanwei{height: 1.1rem;}
	.zhanwei{height: 1.25rem;width: 100%;}
	.guangbo{padding-bottom:.95rem}
	.guangbo .ding .shous{top: .18rem;width: .5rem; }
	.guangbo {background: #f7f7f7; }
	.guangbo{background: #F7F8F7;width: 100%;text-align: left;}
	.guangbo .follow{padding: 0.4rem;height: 1.22rem;}
	.guangbo .follow .inter{height: 1.48rem;width: 1.64rem;margin-left: 0.94rem;float: left;margin-right: 0.3rem;}
	.guangbo .follow h2{margin-top: 0.3rem;}
	.guangbo .follow span{margin-top: 0.15rem;display: block;}
</style> 



