import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import indexx from '../components/Hello'

import shouye from '../pages/shouye/shouye'
import guangbo from '../pages/guangbo/guangbo'
import shuyy from '../pages/shuyy/shuyy'
import wod from '../pages/wod/wod'
import xiaozu from '../pages/xiaozu/xiaozu'

import shuyy_diany from '../pages/shuyy/shuyy_diany'
import shuyy_dushu from '../pages/shuyy/shuyy_dushu'
import shuyy_dians from '../pages/shuyy/shuyy_dians'
import shuyy_tongc from '../pages/shuyy/shuyy_tongc'
import shuyy_yingyue from '../pages/shuyy/shuyy_yingyue'


Vue.use(Router)

export default new Router({
  routes: [
    {path: '*/',component: shouye },
    {path: '/shouye',component: shouye},
    {path: '/guangbo',component: guangbo},
     {path: '/wod',component: wod},
    {path: '/xiaozu',component: xiaozu},
    {path: '/shuyy',component: shuyy,
    	children:[
    			{path: '/',component: shuyy_diany },
					{path:'shuyy_diany', component:shuyy_diany},
					{path:'shuyy_dushu', component: shuyy_dushu},
					{path:'shuyy_dians', component: shuyy_dians},
					{path:'shuyy_tongc', component: shuyy_tongc},
					{path:'shuyy_yingyue', component:shuyy_yingyue}
				]}
  ]
  
})







