<template >
	<div class="box1"  >
			
		<div class="swiper-container" :class='swipeid'>
    		<div class="swiper-wrapper">
		        <slot name='bannerImg1'></slot>
		        <!--<div class="swiper-slide" slot="bannerImg1" >	
					<div class="swiper-zoom-container">
						<img src="../../assets/img1/banner/01.jpg"/>
					</div>	
				</div>
				<div class="swiper-slide" slot="bannerImg1" >
					<div class="swiper-zoom-container">
						<img src="../../assets/img1/banner/02.jpg"/>
					</div>
				</div>
				<div class="swiper-slide" slot="bannerImg1" >
					<div class="swiper-zoom-container">
						<img src="../../assets/img1/banner/03.jpg"/>
					</div>
				</div>-->
				
		    </div>
    		<div :class='{"swiper-pagination":pagination}' :style="{'text-align':paginationDirection}"></div>
   	 	</div>
			
	</div>
</template>

<script>
	import "../../assets/js/swiper.js";
	
export default {
  
props:{
	  swipeid:{		//名称
	type:String,
	default:''
	  },
	  loop:{		//是否循环
	    type:Boolean,
	    default:true
	  },
	direction:{	//滑动方向
	type:String,
	default:'horizontal'//水平  	vertical 垂直
	  },
	autoplay:{	//间隔时间
	    type:Number,
	    default:5000
	  },
	effect:{
	  default:'slide'	//切换效果 slide 默认  "cube" 有阴影  		正方形切"coverflow"	翻页"flip" 	fade（淡入）
	},
	paginationType:{	//分页器类型
	type:String,
	default:'bullets'	//‘bullets’  圆点（默认）‘fraction’  分式 ‘progress’  进度条  ‘custom’ 自定义
	  },
	  pagination:{		//分页器
	    type:Boolean,
	    default:true
	  },
	  paginationDirection:{		//居中
	type:String,
	default:'left'
	  },
	speed:{				//从滑动过程的时间，
	  	default:1000
	},				
	centeredSlides:{	//true 活动快会自动居中
		default:false
	},
	slidesPerView :{	//显示几个
		type:Number,
		default:1
	},
	spaceBetween:{			//图片间的距离
		default:20
	},
	autoplayDisableOnInteraction:{	//用户操作swiper之后，是否禁止autoplay。默认为true：停止。
		default:true
	},
	freeModeSticky:{		//启用边缘贴合
		default:false	
	},
	freeMode:{		//启动可以滑动多格,设置为true则变为free模式，slide会根据惯性滑动且不会贴合
		default:true
	},
	freeModeMomentum:{	//若设置为false则关闭动量，释放slide之后立即停止不会滑动。
		default:true
	},
	freeModeMomentumVelocityRatio:{	//惯性速度
		default:1
	},
	freeModeMomentumBounce:{		//启用反弹
		default:false
	},
	freeModeMomentumBounceRatio:{		//反弹力度
		default:0
	}
	
    },
    mounted:function(){
      var _this=this;
      new Swiper('.'+_this.swipeid,{
        centeredSlides:_this.centeredSlides,
        slidesPerView:_this.slidesPerView,
        spaceBetween:_this.spaceBetween,
        loop:_this.loop,
        pagination:".swiper-pagination",
        direction:_this.direction,
        autoplay:_this.autoplay,
        effect:_this.effect,			
        speed:_this.speed,				//从滑动过程的时间，
  		autoplayDisableOnInteraction:_this.autoplayDisableOnInteraction,	
  		freeModeSticky:_this.freeModeSticky,
  		freeMode : _this.freeMode,	
  		freeModeMomentum:_this.freeModeMomentum,
  		freeModeMomentumVelocityRatio:_this.freeModeMomentumVelocityRatio,	
  		freeModeMomentumBounce:_this.freeModeMomentumBounce,	
  		freeModeMomentumBounceRatio:_this.freeModeMomentumBounce,	
  		zoom:true,	//放大缩小
  		grabCursor:true,	//设置为true时，鼠标覆盖Swiper时指针会变成手掌形状，拖动时指针会变成抓手形状。
      });
    }
  }

</script>

<style  >
	@import "../../assets/css/swiper.css";
	
	 
</style> 



