<template id="">
		<div class="wod" >
			<ding txts="我的" wod='true' > </ding>
			<div class="zhanwei">
				
			</div>
			<wodshang>
				<img src="../../assets/img1/ic_wallet_header_background.png" slot='img' class="img"/>
				<img src="../../assets/img1/wodtx.jpg" slot='headimg' class="headimg"/>
				<h2 class="name" slot= 'name'>pengk★渊</h2>
				<p class="id" slot='id'>ID:<span>1162212711</span></p>
				<p class="homepage" slot='homepage'>个人主页></p>
				<p class="follow" slot='follow'>关注<span>50</span></p>
				<p class="befollow" slot='befollow'>被关注<span>110万</span></p>
			</wodshang>

		</div>
</template>


<script>
	import ding from"../../components/ding"
	import wodshang from './wodshang'
export default {
	components:{
  	ding,
  	wodshang
  },
  props:["xia","xiab","jilu"],
		computed:{
			bu:function(){
				if(this.xiab==this.jilu){
					return false
				}
				return true
			}
		},
		methods:{
			fn:function(){
				//console.log( "xia:"+this.xiab  )
				//console.log( "点击:"+this.jilu  )
				this.$emit("dianjii",this.xiab)
			}
	}
}

</script>

<style>
	.wod{background:#f5f1ee;padding-bottom: 1.2rem;}
</style> 



