<template>
	<div class="dynamyc clearfix">
		<div class="portrait">
			<span class="close"><img src="../../assets/img1/ic_music_player_close.png"/></span>
			<span><slot name = 'dynamycImg'></slot></span>
		</div>
		<div class="dynamycWrap">
			<div class="datad">
				<div class="dynamycia " @click="fn" :class="{ 'bai':bu }" >{{ txt5 }}</div>
				<div class="attention">
					<h3>{{ txt3 }}</h3>
					<span>{{ txt4 }}人关注</span>
				</div>
			</div>
			<p><slot name = 'texted'></slot></p>
			<ul>
				<li><img src="../../assets/img1/ic_liked.png"/><p><slot name = 'linek'></slot></p></li>
				<li><img src="../../assets/img1/ic_group_comment_0.png"/><p><slot name = 'gares'></slot></p></li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		data : function (){
			return {
				bu:false,
				txt5:"关注"
			}
		},
		props : [ 'txt3','txt4','jilu'],
		computed:{
			/*bu:function(){
				if(this.txt3==this.jilu){
					//return true
				}
				return false
			}*/
		},
		methods:{
			fn:function(){
				//console.log( "xia:"+this.xiab  )
				//console.log( "点击:"+this.jilu  )
				//this.$emit("dianjii",this.txt3);
				this.bu=true;
				this.txt5="✔已关注"
				
			}
	}
	}
</script>

<style>
	.dynamyc{ position: relative; width: 6.46rem; padding: 0.35rem 0 0.35rem 0.3rem; background: #fff;margin: 0 auto;border-radius: 0.08rem;box-shadow: 0 0 0.06rem 0.06rem #eee;margin-top: 0.2rem;}
	.dynamyc .close{width: 0.22rem;height: 0.22rem;position: absolute;top: 0.2rem;right: 0.2rem;}
	.dynamyc .close img{width: 0.22rem;height: 0.22rem;}
	.portrait{float: left;width: 1.18rem;height: 100%;}
	.portrait span{width: 0.97rem;height: 0.97rem;}
	.portrait span img{width: 0.97rem;height: 0.97rem; border-radius: 50%;}
	.dynamycWrap{float: left;width :4.74rem;}
	.dynamycWrap .datad .bai { background: #fff; color: #ccc; }
	.dynamycWrap .dynamycia{font-size: 0.24rem; margin-top: 0.2rem; float: right;width: 1.44rem;height: 0.54rem;background: #42c055;border-radius: 0.05rem;text-align: center;color: #fff;line-height: 0.54rem;}
	.dynamycWrap p{font-size: 0.28rem;line-height: 0.4rem;font-family: "Microsoft YaHei";}
	.att_right_top{height: 1.03rem;}
	.attention h3{font-size: 0.32rem;line-height: 0.58rem;}
	.attention span{font-size: 0.2rem;line-height: 0.47rem;color: #8b8b8b;}
	.dynamycWrap ul{margin-top: 0.24rem;list-style: none;}
	.dynamycWrap ul li{float: left;width: 0.80rem;color:#999;padding-right: 0.2rem;margin-right: 0.07rem;}
	.dynamycWrap ul li p{float: right;}
	.dynamycWrap ul li img{width: 0.38rem;height: 0.38rem;}
</style>


